import React from "react";
import { Link } from "gatsby";
import { Card } from "react-bootstrap";
// import ImageFallback from "../../components/common/image-fallback";
import { StaticImage, GatsbyImage, getImage } from "gatsby-plugin-image";
// import img from "../../images/blog/unraveling-defi.png"

const BlogsData = (data) => {
  const { data: propsData } = data;
  console.log("propsData::", propsData);
  return (
    <section className="section-develop" id="ourteam">
      <div className="container grid">
        <h3 className="bottomAnimation heading-tertiary servicehead">Blogs</h3>
      </div>
      <div className="container">
        <div className="row">
          {propsData?.edges?.map((ele, ind) => {
            const { node } = ele;
            console.log("node img::", node.image);
            // const image = getImage(node.image.childImageSharp?.gatsbyImageData);
            // console.log("getImage::", image);
            // const blogImg = "../.." + node.image;
            // console.log("blogImg::", blogImg);
            return (
              <div className="col-md-4 col-sm-6 ">
                <Card
                  key={ind}
                  className="developer card border rounded shadow"
                >
                  <Link to={`${node.slug}`} className="text-decoration-none">
                    {/* <Card.Img
                      className="card-img w-100 rounded-5 pb-3"
                      src={node.image}
                      alt="Members"
                      // width={200}
                      // height={300}
                    /> */}
                    {/* <ImageFallback
                      src={node.image}
                      alt="Members"
                      className="card-img w-100 rounded-5 pb-3"
                    /> */}

                    {/* once image add in src/images/img-src after add to src. for time being and check the below the condition */}
                    {node?.slug === "unraveling-defi" && (
                      <StaticImage src={"../../images/blog-1.png"} alt={"unraveling-defi"} />
                    )}
                    {node?.slug === "crypto" && (
                      <StaticImage src={"../../images/blog-2.png"} alt={"crypto"} />
                    )}
                     {node?.slug === "blockchain" && (
                      <StaticImage src={"../../images/blog-3.png"} alt={"blockchain"} />
                    )}
                  </Link>
                  <Card.Body>
                    <div className="developer-inner">
                      <div className="d-flex justify-content-between align-items-center">
                        <Link
                          to={`${node.slug}`}
                          className="text-decoration-none text-muted"
                        >
                          <h4 className="mb-4 text-dark fs-semibold pt-2">{node.title.slice(0,40)}{"..."}</h4>
                        </Link>
                      </div>
                      <p className="cta-text mb-2 pt-2">
                        {node?.short_description.slice(0, 85)}{" "}
                        <Link to={`${node.slug}`}>Read More</Link>
                      </p>
                      <p className="fs-4">{node.blog_created_date}</p>
                    </div>
                  </Card.Body>
                  {/* <div className="card-footer border-0 text-end bg-transparent py-3">
                    <Link
                      to={`${node.author_name}`}
                      className="text-decoration-none  text-muted"
                    >
                      <span className="fs-4 text-decoration-underline">
                        Read more{" "}
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="13"
                          height="13"
                          fill="currentColor"
                          class="bi bi-chevron-right"
                          viewBox="0 0 16 16"
                        >
                            <path
                              fill-rule="evenodd"
                              fontWeight={"bold"}
                              d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"
                            />
                        </svg>
                      </span>
                    </Link>
                  </div> */}
                </Card>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default BlogsData;
